#advantages {
  color: var(--white-shade);
  display: flex;
  justify-content: center;
  margin-bottom: 81px;
}

@media screen and (min-width: 768px) {
  #advantages {
    margin-bottom: 204px;
  }
}

@media screen and (min-width: 1200px) {
  #advantages {
    margin-bottom: 177px;
  }
}

.advantages-center {
  display: flex;
  align-items: center;
  justify-content: center;
}

#advantages ul {
  display: flex;
  flex-direction: column;
  gap: 32px;
}

@media screen and (min-width: 768px) {
  #advantages ul {
    flex-direction: row;
    gap: 80px;
  }
}

@media screen and (min-width: 1200px) {
  #advantages ul {
    gap: 32px;
  }
}

#advantages ul li {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  width: 230px;
}

@media screen and (min-width: 768px) {
  #advantages ul li {
    width: 152px;
    gap: 12px;
  }
}

@media screen and (min-width: 1200px) {
  #advantages ul li {
    width: 368px;
    /* height: 52px; */
  }
}

.advantages-big {
  text-align: center;
  font-size: 36px;
  font-weight: 700;
  letter-spacing: 1.44px;
  text-transform: uppercase;
}

@media screen and (min-width: 768px) {
  .advantages-big {
    text-transform: none;
    font-size: 48px;
    letter-spacing: 1.92px;
  }
}

@media screen and (min-width: 1200px) {
  .advantages-big {
    font-size: 58px;
    letter-spacing: 2.32px;
  }
}

.advantages-small {
  text-align: center;
  font-size: 12px;
  font-weight: 400;
  line-height: 170%;
}

@media screen and (min-width: 768px) {
  .advantages-small {
    font-size: 14px;
    line-height: 174%;
  }
}

@media screen and (min-width: 1200px) {
  .advantages-small {
    font-size: 16px;
    line-height: 176%;
    letter-spacing: 0.32px;
  }
}

