.advantages-big::before {
  content: url('../images/advantages/cow.svg');
  background-color: var(--yellow);
  width: 50px;
  height: 50px;
  border-radius: 25;
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  margin-bottom: 16px;
}

@media screen and (min-width: 768px) {
  .advantages-big :before {
    margin-bottom: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .advantages-big :before {
    margin-bottom: 40px;
  }
}

#advantages ul li:first-child .advantages-big::before {
  content: url('../images/advantages/cow.svg');
}

#advantages ul li:nth-child(2) .advantages-big::before {
  content: url('../images/advantages/weight.svg');
}

#advantages ul li:last-child .advantages-big::before {
  content: url('../images/advantages/ice-cream.svg');
}
